<template>
    <div id="app">
      <main>
        
      <div class="search-box">
        <input 
          type="text" 
          class="search-bar" 
          placeholder="search..."
          v-model="query"
          @keypress="fetchWeather"
          />
      </div>

      <div class="weather-wrap" v-if="weather?.main != undefined">
        
        <div class="location-box">

          <div class="location">
            {{weather.name}}, {{weather.sys.country}} local
          </div>

          <div class="date">
            {{ dateBuilder() }}
          </div>

        </div>

        <div class="weather-box">

          <div class="temp">
            {{ Math.round(weather.main.temp) }}ÂºC
          </div>

          <div class="weather">
            {{ weather.weather[0].main }}
          </div>

        </div>
      </div>

      </main>

    </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      api_key:"022deed91f218a32b057eeeb8f2d7c00",
      url_base: "https://api.openweathermap.org/data/2.5/",
      query: '',
      weather: {},
    }
  },
  methods:{
      fetchWeather(e) {
        if(e.key === "Enter"){
          console.log(this.query)
          fetch(`${this.url_base}weather?q=${this.query}&units=metric&appid=${this.api_key}`)
          .then(res => {
            return res.json();
          })
          .then(res => {
            this.setResults(res)
          })
        }
      },
      setResults(results){
        this.weather = results
      },
      dateBuilder() {
        let data = new Date();
        let mes = data.getMonth()+1;
        let dia = data.getDate();
        let ano = data.getFullYear();
        return `${dia}/${mes}/${ano}`
      }
    }
}
</script>

